import qs from 'qs';
import React, { useEffect, useState } from 'react';
import { useSetRecoilState } from 'recoil';
import data from '../core/data';
import { mainMenuState } from '../core/store';
let courseData: any = {};
// let curropenid = '';
const CourseERTServe = () => {
    const setMainMenu = useSetRecoilState(mainMenuState);
    const [curropenid, setcurropenid] = useState('');
    const [isReady, setIsReady] = useState(false);

    useEffect(() => {
        setMainMenu('writing');
        const query = qs.parse(data.getOnlyquerystring());
        const openid = (query && query.openid) ? query.openid as string : '';
        setcurropenid(openid);
        courseData = data.courseToMainJSONWithKey(openid, 'openID')
        console.log('courseData-curropenid0', courseData.item, curropenid)
        setIsReady(true);
    }, [])

    let CompItem: any = null;
    if (courseData && courseData.item) {
        CompItem = courseData.item.map((item: any, i: number) => {
            const arr = String(item.tit).split(',');
            return <li onClick={() => {
                // console.log('tmplog_item', item.query)
                data.gotoPage('ertd', '?d=' + item.query)
            }} className="item" key={'li' + i}>
                <div>{arr[0]}</div>
                <div>{arr[2]}</div>
            </li>
        })
    }

    console.log('courseData-curropenid1', courseData.item, curropenid)
    return (
        <div className="CourseERTServe">
            {(isReady && CompItem) && <ul className="">
                {CompItem}
            </ul>
            }
        </div>
    );
};

export default CourseERTServe;