import classnames from 'classnames';
import { css } from 'emotion';
import qs from 'qs';
import React, { useEffect, useState } from 'react';
import { atom, useRecoilValue, useSetRecoilState } from 'recoil';
import stringSimilarity from 'string-similarity';
import MDictation2 from '../components/MDictation2';
import data from '../core/data';
import { mainMenuState } from '../core/store';
// import CourseERTMain from './CourseERTMain';

type TCheckedEnKo = {
    "tag": string,
    "en": string,
    "en_": string,
    "ko": string,
    "words": any,
    "pattern": string,
}
let ertjson: {
    info: any,
    sentense: TCheckedEnKo[]
} = require('../resource/tempert.json')

// let currentWCID = 'ertCourse001';
let currErtid = '';

const coursePrimaryStruct = {
    "sentenseDoneArr": []
}

let storageData: any = {
    ertCourse001: {
        "sentenseDoneArr": []
    }
}
const updateDoneItemState = atom({
    key: "updateDoneItemState",
    default: 0,
})

const initStorageState = () => {
    // ertCourse001
    let getData = (window as any).localStorage.getItem('ertdata');
    if (!getData) {
        // 
        storageData = {};
        if (!storageData[currErtid]) storageData[currErtid] = Object.assign({}, coursePrimaryStruct);
        // getData = storageData[currentWCID];
    } else {
        //   
        storageData = JSON.parse(getData);
        //     
        if (!storageData[currErtid]) storageData[currErtid] = Object.assign({}, coursePrimaryStruct);
    }
    syncStorageState();
}
const syncStorageState = () => {
    (window as any).localStorage.setItem("ertdata", JSON.stringify(storageData));

}

const setDoneItem = (inWCID: string, itemIdx: number) => {
    const checkDouble = storageData[inWCID].sentenseDoneArr.filter((item: number) => {
        return item !== itemIdx;
    })
    storageData[inWCID].sentenseDoneArr = checkDouble;//   
    storageData[inWCID].sentenseDoneArr.push(itemIdx);// 
    syncStorageState();
}
const setUndonItem = (inWCID: string, itemIdx: number) => {
    const checkDouble = storageData[inWCID].sentenseDoneArr.filter((item: number) => {
        return item !== itemIdx;
    })
    storageData[inWCID].sentenseDoneArr = checkDouble;//  
    syncStorageState();
}
const CourseERT = () => {
    const updateDoneItem = useRecoilValue(updateDoneItemState);
    const [viewMode, setViewMode] = useState('study');
    const [isData, setisData] = useState(false);
    const setMainMenu = useSetRecoilState(mainMenuState);
    useEffect(() => {
        setMainMenu('writing');

        // http://localhost:9822/#/ert?ertid=normalC1

        // alert(currErtid);
        const href = window.location.href;

        let queryViewMode = '';
        //  url 
        if (href.indexOf('?d=') > 0) {
            const decryptQuery = data.parseQuery();
            let cid = (decryptQuery && decryptQuery.cid) ? decryptQuery.cid : '';
            let tpid = (decryptQuery && decryptQuery.tpid) ? decryptQuery.tpid : '';
            queryViewMode = (decryptQuery && decryptQuery.mode) ? decryptQuery.mode : '';

            data.getTopicJSON(cid, tpid).then((result: any) => {
                ertjson = result.data;
                currErtid = tpid;
                //   
                initStorageState();
                setisData(true);
            }).catch((err: any) => {
                currErtid = '';
                //   
            });
        } else {
            const query = qs.parse(data.getOnlyquerystring());
            currErtid = (query && query.ertid) ? query.ertid as string : '';
            if (currErtid) {
                data.getERTJSON(currErtid).then((result: any) => {
                    ertjson = result.data;
                    //   
                    initStorageState();
                    setisData(true);
                }).catch((err: any) => {
                    currErtid = '';
                    //   
                });

            } else {
                currErtid = '';
                //   

            }
        }
        if (queryViewMode) {
            setViewMode(queryViewMode);
        }

    }, []);

    return (
        <div className="CourseERT">
            <ul className="tabs">
                <li className={classnames(["tab", { active: viewMode === 'study' }])} onClick={() => {
                    setViewMode('study');
                }}></li>
                <li className={classnames(["tab", { active: viewMode === 'testease' }])} onClick={() => {
                    setViewMode('testease');
                }}><div><br></br>
                        <span className="small">(easy)</span></div></li>
                <li className={classnames(["tab", { active: viewMode === 'testhard' }])} onClick={() => {
                    setViewMode('testhard');
                }}><div><br></br>
                        <span className="small">(hard)</span></div></li>
            </ul>
            {
                isData && <>
                    <CompleteItem />
                    {viewMode === 'study' && <ListMode />}
                    {viewMode === 'testease' && <TestEaseMode />}
                    {viewMode === 'testhard' && <TestHardMode />}
                </>
            }
        </div>

    );
};
const CompleteItem = () => {
    const doneArr = storageData[currErtid].sentenseDoneArr.length;
    return (<div className="CompleteItem">
        <div className="gauge-group">
            <div className="txt">
                {
                    doneArr > 0 && <>
                        {doneArr}
                        <span className="divide">/</span>
                    </>
                }
                <span className="tot">{ertjson.sentense.length}</span></div>
            <div className={classnames(["gauge", css`
                width: ${(doneArr / ertjson.sentense.length) * 100}%
            `])}></div>
        </div>
    </div>);
}

const PrintMode = () => {
    const Compo = ertjson.sentense.map((item: TCheckedEnKo, i: number) => {
        return <tr>
            <td className="td1">{i + 1}</td>
            <td className="td2">
                {item.ko}<br></br>
                <span className="placeholder"> </span>

            </td>
            <td className="td3">
                <div>
                    <div className="check-box ex"></div>
                    <div className="check-box">v</div>
                    <div className="check-box">v</div>
                    <div className="check-box">v</div>
                    <div className="check-box">v</div>
                </div>
            </td>
            <td className="td4">{item.en}</td>
            <td className="td5">&nbsp;</td>
        </tr>
    })
    return (<div className="PrintMode">
        <button onClick={() => {
            (window as any).printScript('#table-english-writing');
        }}></button>
        <table className="print-table" id="table-english-writing">
            <thead>
                <td>no</td>
                <td>  </td>
                <td></td>
                <td></td>
                <td></td>
            </thead>
            <tbody>
                {Compo}
            </tbody>
        </table>

    </div>);
}
const ListMode = () => {
    const Compo = ertjson.sentense.map((item: TCheckedEnKo, i: number) => {
        return <ListModeItem data={item} idx={i} key={`ListModeItem${i}`} />
    })
    return (<ul className="ListMode">
        {Compo}
    </ul>);
}

const ListModeItem = ({ data, idx }: { data: TCheckedEnKo, idx: number }) => {
    const [isBack, setIsBack] = useState(false);
    // let isDone = storageData[currErtid].sentenseDoneArr.includes(idx);
    const [thisDone, setThisDone] = useState(storageData[currErtid].sentenseDoneArr.includes(idx))
    const setupdateDoneItem = useSetRecoilState(updateDoneItemState);
    // 
    const setDone = (inWCID: string, inIdx: number) => {
        setDoneItem(inWCID, inIdx);

        setTimeout(() => {
            setupdateDoneItem(Math.random());
            setThisDone(true);
        }, 100);
    }
    //  
    const setUndone = (inWCID: string, inIdx: number) => {
        setUndonItem(inWCID, inIdx);
        setTimeout(() => {
            // setenInput('');
            // setisChecked(false);
            setupdateDoneItem(Math.random());
            setThisDone(false);
        }, 100);
    }
    const turn = () => {
        if (thisDone) return;
        setIsBack(!isBack);
    }
    return (<li>
        <div className={classnames(["flip-box", { back: isBack }, css`
            height: 200px !important;
        `])}>
            <div className={classnames(["flip-box-inner", { done: thisDone }])}>
                <div className="flip-box-front">
                    {/* {isDone && <div className="done"></div>} */}
                    <div className="flip-box-in">
                        <div className="top" onClick={() => {
                            turn();
                        }}><div className="no">{idx + 1}</div></div>
                        <div className="middle" onClick={() => {
                            turn();
                        }}>{data.ko}</div>
                        <div className="footer">
                            {
                                thisDone
                                    ?
                                    <div className="done">
                                        <span className="label-done"></span>
                                        <button className="btn btn-border btn-redo" onClick={() => {
                                            setUndone(currErtid, idx);
                                        }}><i className="fas fa-redo"></i></button>
                                    </div>
                                    :
                                    <div className="study">
                                        <button className="btn btn-done" onClick={() => {
                                            setDone(currErtid, idx);
                                        }}><i className="fas fa-check-circle"></i></button>
                                    </div>
                            }
                        </div>
                    </div>


                </div>
                <div className="flip-box-back">
                    <div className="flip-box-in">
                        <div className="top" onClick={() => {
                            turn();
                        }}><div className="no">{idx + 1}</div></div>
                        <div className="middle" onClick={() => {
                            turn();
                        }}>{data.en}</div>
                        <div className="footer"></div>

                    </div>
                </div>
            </div>
        </div>
    </li>);
}

const getLevel = (tagStr: string) => {

    if (!tagStr) return '';
    if (tagStr.includes('lv1')) return 'lv1';
    if (tagStr.includes('lv2')) return 'lv2';
    if (tagStr.includes('lv3')) return 'lv3';
    if (tagStr.includes('lv4')) return 'lv4';
    if (tagStr.includes('lv5')) return 'lv5';
    return '';
}

const TestEaseMode = () => {
    const CompSentenseItem = ertjson.sentense.map((item: any, i: number) => {
        return <SentenseEaseItem data={item} key={'SentenseItem' + i} idx={i} />
    })
    return (<div className="TestMode">
        <ul className="test-cards">
            {CompSentenseItem}
        </ul>
        <button></button>
        <button></button>
    </div>);
}

const TestHardMode = () => {
    const CompSentenseItem = ertjson.sentense.map((item: any, i: number) => {
        return <SentenseHardItem data={item} key={'SentenseItem' + i} idx={i} />
    })
    return (<div className="TestMode">
        <ul className="test-cards">
            {CompSentenseItem}
        </ul>

    </div>);
}

const SentenseHardItem = ({ data, idx }: { data: TCheckedEnKo, idx: number }) => {
    // isDone :   
    // isChecked :  
    const [enInput, setenInput] = useState('');
    const setupdateDoneItem = useSetRecoilState(updateDoneItemState);
    const onChange = (e: any) => {
        setenInput(e.target.value);
    }
    const onEnterPress = (e: any) => {
        if (e.keyCode === 13) {
            e.preventDefault();
            setisChecked(true);
        }
    }
    const [isChecked, setisChecked] = useState(false);
    const [showHint2, setShowHint2] = useState(false);
    const [thisDone, setThisDone] = useState(false);
    const lv = getLevel(data.tag);
    const correctPercent = parseInt(String(stringSimilarity.compareTwoStrings(data.en, enInput) * 100));
    // 
    const setDone = (inWCID: string, inIdx: number) => {
        setDoneItem(inWCID, inIdx);
        setShowHint2(false);
        setTimeout(() => {
            setupdateDoneItem(Math.random());
            setThisDone(true);
        }, 100);
    }
    //  
    const setUndone = (inWCID: string, inIdx: number) => {
        setUndonItem(inWCID, inIdx);
        setTimeout(() => {
            setenInput('');
            setisChecked(false);
            setupdateDoneItem(Math.random());
            setThisDone(false);
        }, 100);
    }
    let isDone = storageData[currErtid].sentenseDoneArr.includes(idx) || thisDone;
    let stateClass: 'wrong' | 'correct' | 'done' | '' = '';
    //  
    if (isChecked) {
        if (correctPercent > 94) {
            stateClass = 'correct';
        } else {
            stateClass = 'wrong';
        }
    }
    if (isDone) stateClass = 'done';
    return (<li className={classnames(["card", stateClass])}>
        <div className="no">{idx + 1}</div>
        <div className="card-in">

            <span className="display-none">{thisDone}</span>
            <div>
                {/* <div className="label"></div> */}
                {data.ko}
            </div>
            {
                isDone
                    ?
                    <div>{data.en}</div>
                    :
                    <>
                        {
                            isChecked
                                ?
                                <div className="check-result">
                                    <div className="label"> </div>
                                    <CheckedEnKoA data={data} enInput={enInput} key={`sent1_${idx}`} type="x" />
                                    <div className="label">({correctPercent}% )</div>
                                    {correctPercent > 80 && <button className="btn btn-border btn-done" onClick={() => {
                                        setDone(currErtid, idx)
                                    }}><i className="fas fa-check-circle"></i></button>}

                                    <CheckedEnKoA data={data} enInput={enInput} key={`sent2_${idx}`} type="o" />
                                </div>
                                :
                                <div className="check-before">
                                    <div className="label"> </div>
                                    <textarea className="input" value={enInput} onChange={onChange} onKeyDown={onEnterPress}></textarea>

                                </div>
                        }
                    </>
            }
            {(data.words && data.words.length > 0 && showHint2) && <Words wordArr={data.words} />}
            {
                (data.words && data.words.length > 0) && <>
                    {
                        showHint2
                            ?
                            <button className="btn btn-word" onClick={() => {
                                setShowHint2(false);
                            }}><i className="fas fa-minus"></i></button>
                            :
                            <button className="btn btn-word" onClick={() => {
                                setShowHint2(true);
                            }}><i className="fas fa-plus"></i>({data.words.length})</button>
                    }

                </>
            }
            <div className="footer">
                {
                    isDone
                        ?
                        <div className="done">
                            <span className="label-done"></span>
                            <button className="btn btn-border btn-redo" onClick={() => {
                                setUndone(currErtid, idx);
                            }}><i className="fas fa-redo"></i></button>
                        </div>
                        :
                        <div className="study">
                            {
                                isChecked
                                    ?
                                    <button className="btn btn-border btn-redo" onClick={() => {
                                        setisChecked(false);
                                        setenInput('');
                                    }}><i className="fas fa-redo"></i></button>
                                    :
                                    <>
                                        <button className="btn btn-done" onClick={() => {
                                            setDone(currErtid, idx);
                                        }}><i className="fas fa-check-circle"></i></button>

                                        <button className="btn" onClick={() => {
                                            let hintStr = data.en_;
                                            if (!hintStr) {
                                                const arr = data.en.split(' ');
                                                if (lv === 'lv1') {
                                                    hintStr = arr[0] + ' ' + arr[1] + ' ';
                                                } else if (lv === 'lv2') {
                                                    hintStr = arr[0] + ' ' + arr[1] + ' ' + arr[2] + ' ';
                                                } else {
                                                    hintStr = arr[0] + ' ' + arr[1] + ' ' + arr[2] + ' ' + arr[3] + ' ';
                                                }
                                            }
                                            setenInput(hintStr);
                                        }}><i className="fas fa-search"></i></button>
                                        <button className="btn" onClick={() => {
                                            setisChecked(true);
                                        }}><i className="fas fa-check"></i></button>

                                    </>
                            }
                        </div>
                }
            </div>
        </div>

    </li>);
}
