const onlyWord = (word: string) => {
    word = word.replace("'s", "");
    word = word.replace(".", "");
    word = word.replace("!", "");
    word = word.replace("?", "");
    return word.toLowerCase();
}
type TWordInfo = {
    slim: string,
    oriDP: string,
}
const CheckedEnKoA = ({
    data,
    enInput,
    type,
}: {
    data: TCheckedEnKo,
    enInput: string,
    type: 'o' | 'x'
}) => {
    const tmpInput = enInput;
    const tmpEn = data.en;

    let dpStr = ''//  
    let dpStr2 = ''//  
    if (type === 'x') {
        // 
        dpStr = tmpInput;
        dpStr2 = tmpEn;
    } else if (type === 'o') {
        // 
        dpStr = tmpEn;
        dpStr2 = tmpInput;
    }
    const tmpInputArr = dpStr.split(' ');
    const tmpEnArr = dpStr2.split(' ');
    //     
    const tmpInputArr2: TWordInfo[] = tmpInputArr.map((word: string, i: number) => {
        return {
            slim: onlyWord(word),
            oriDP: word,
        }
    });
    //     
    const tmpEnArr2: TWordInfo[] = tmpEnArr.map((word: string, i: number) => {
        return {
            slim: onlyWord(word),
            oriDP: word,
        }
    });
    let resultA: any = [];
    const isRightWords = (word: string, idx: number) => {
        let compareWord = " " + word;
        if (idx === 0) {
            compareWord = word;
        } else {
            compareWord = " " + word;//      
        }
        let makeSentnse = ' ';
        tmpEnArr2.forEach((word: TWordInfo, i: number) => {
            makeSentnse += word.slim + ' ';
        })
        console.log('makeSentnseA', makeSentnse, word, makeSentnse.includes(compareWord))
        return makeSentnse.includes(compareWord);
    }
    let wordPlus: string[] = [];
    tmpInputArr2.forEach((word: TWordInfo, i: number) => {
        wordPlus.push(word.slim);
        const check = isRightWords(word.slim, i);
        console.log('check:', check, word.slim)
        let checkClass = "";
        //      
        if (!check) {
            wordPlus = [];
            checkClass = type === 'x' ? "wrong" : "correction";
        }
        resultA.push(<span className={checkClass}>{word.oriDP + ' '}</span>);
    })
    return (<div className="inputEn">
        <div>{resultA}</div>
    </div>)
}

const Words = ({ wordArr }: { wordArr: any }) => {
    const Comp = wordArr.map((item: any, i: number) => {
        return <li className="word-item">
            <div className="en">{item[0]}</div>
            <span className="divide">::</span>
            <div className="means">{item[1]}</div>
        </li>
    })
    return (
        <ul className="Words">
            {Comp}
        </ul>
    );
}

export default CourseERT;