import React, { useEffect } from 'react';
import { useSetRecoilState } from 'recoil';
import { mainMenuState } from '../core/store';
import data from '../core/data';
import classnames from 'classnames';
import { css } from 'emotion';

const CourseERTMain = () => {
    const setMainMenu = useSetRecoilState(mainMenuState);

    useEffect(() => {
        setMainMenu('writing');
    }, []);

    const CompCate = data.ertJSON.map((json: any, i: number) => {
        return <Category json={json} key={'cate' + i} idx={i} />
    })

    return (
        <div className="CourseERTMain">
            {CompCate}
        </div>
    );
};

const Category = ({
    json,
    idx,
}: {
    json: any,
    idx: number,
}) => {
    const CompItem = json.items.map((obj: any, jIdx: number) => {
        return <li className="grid-item" key={"compitem" + jIdx}>
            <div onClick={() => {
                if (obj.type === 'shadowing') {
                    data.gotoPage('ertserve', obj.query)
                } else {
                    data.gotoPage('ertd', obj.query)
                }

            }} className={classnames(["grid-item-in", css`
            background-color:${obj.color}
        `])} >
                <i className={`icon ${obj.icon}`}></i>

                <div className="item-title">{obj.title}</div>
            </div>
        </li>
    })
    return (<div className="Category">
        <div className="category-title">{json.desc}</div>
        <ul className="grid-container">
            {CompItem}
        </ul>
    </div>)
}

export default CourseERTMain;